import { AttachMoney } from "@rp/react/web/mui/icons"
import "@tanstack/react-table"
import { companyPriceChanges } from "../endpoints/companyPriceChanges"
import { companyQuote } from "../endpoints/companyQuote"
import { genericTable } from "../genericTable"
import { createRealTimeHeaderInfo } from "../utils/cacheTimeHelper"

const pricing = genericTable({
  tableName: "pricing",
  tableLabel: "Pricing",
  tableIcon: <AttachMoney />,
  defaultVisible: [
    { endpoint: "companyQuote", columnKey: "price" },
    { endpoint: "companyQuote", columnKey: "changePercentage" },
    { endpoint: "companyPriceChanges", columnKey: "5D" },
    { endpoint: "companyPriceChanges", columnKey: "1M" },
  ],
  headerInfo: createRealTimeHeaderInfo("Price", "companyQuote"),
  columns: [
    companyQuote.price("pricing"),
    companyQuote.changePercentage("pricing"),
    companyPriceChanges.oneWeek("pricing"),
    companyPriceChanges.oneMonth("pricing"),
    companyPriceChanges.threeMonths("pricing"),
    companyPriceChanges.sixMonths("pricing"),
    companyPriceChanges.ytd("pricing"),
    companyPriceChanges.oneYear("pricing"),
    companyPriceChanges.threeYears("pricing"),
    companyPriceChanges.fiveYears("pricing"),
    companyPriceChanges.tenYears("pricing"),
    companyPriceChanges.max("pricing"),
  ],
})

export { pricing }
